.addTo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  width: 256px;
  height: 40px;
  padding: 8px 12px;
  font-family: 'Manrope';
  font-weight: 700;
  font-size: 16px;
  line-height: 1.37;
  letter-spacing: 0.04em;
  color: #fef9f9;
  background-color: #54adff;
  border: 2px solid #54adff;
  border-radius: 40px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    color: #fef9f9;
    background-image: linear-gradient(290.46deg, #419ef1 0%, #9bd0ff 107.89%);
    border: 2px solid #ffffff;
  }

  @media screen and (min-width: 768px) {
    margin-right: 17px;
  }
}

.heartIcon {
  stroke: #fef9f9;
  margin-left: 8px;

  .addTo:hover &,
  .addTo:focus & {
    animation: beat 1s infinite;
  }
}

@keyframes beat {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
